.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

div#info_pane {
  width: 400px ;
  float:right ;
}


html, body, div#root, div#root > div {
  height: 100% ;
}

xxxdiv#root > div:first-child > div:nth-child(2) {
  overflow-x: hidden;
  position: relative;
  height: 100%;
}


li::marker {
}

div#info_pane {
  background-color: antiquewhite ;
  height: 100%
}


div#info_pane.MachineDetails {
  background-color: antiquewhite
}

